@import url(<https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap>);
*{margin:0;padding:0;outline:0;border:0;box-sizing:border-box}*:focus{outline:0}html,body,#root{height:100%}body{-webkit-font-smoothing:antialiased}body,input,button{font:14px 'Roboto', sans-serif}a{text-decoration:none}ul{list-style:none}button{cursor:pointer}p{font-size:1rem}.g_fl{float:left}.g_fr{float:right}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;margin-bottom:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New',  monospace}

.main-outlet{min-height:800px}.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.accountModal{z-index:9999;width:700px;height:500px;background-color:#2E294E;position:absolute;top:50%;left:50%;margin:-250px 0 0 -350px;display:flex;flex-direction:row}.accountModal .account-benifits{width:50%;background-color:#D7263D}.accountModal .account-entry{width:50%;text-align:center;color:white}.accountModal .account-entry-connectButton{width:225px;padding:10px;border-radius:20px;display:block;margin:0 auto;font-weight:500;-webkit-transition:background-color .25s;transition:background-color .25s}@media only screen and (min-width: 600px){.accountModal .account-entry-connectButton{margin-top:15px}}.accountModal .account-entry-connectButton.facebook{background-color:#2d88ff}.accountModal .account-entry-connectButton.facebook:hover{background-color:#147aff}.accountModal .account-entry-connectButton.twitter{background-color:#00ACEE}.accountModal .account-entry-connectButton.twitter:hover{background-color:#009ad5}.accountModal .account-entry-connectButton.google{border:1px solid}.accountModal .account-entry-connectButton.google:hover{background-color:#d3d3d3}.nav_spacer{width:100vw;height:40px;background-color:white}.nav_leadbar{width:100vw;padding:10px;background-color:red;z-index:9998;position:fixed;top:0;opacity:1;background-color:rgba(206,206,206,0.75)}.nav_leadbar .nav_leadbard_links button{margin-right:10px}.nav_leadbar .nav_leadbard_links button:last-child{margin-right:0px}#nav__sidebar{width:280px;height:100vh;background-color:#CAC4CE;box-shadow:0px 0px 15px 0px rgba(0,0,0,0.75);position:fixed;top:0;left:-290px;z-index:9999;-webkit-transition:.5s;transition:.5s}#nav__sidebar.open{left:0}#nav__sidebar .__closeBarWrapper{width:100%;padding:10px 20px;text-align:right}#nav__sidebar .__closeBar{width:20px;height:20px}#nav__sidebar .__closeBar img{width:100%;height:100%}

footer{background-color:#CAC4CE;border-top:5px solid #D4E79E}footer ._navigation{padding:10px 50px}footer ._navigation .__direct{width:60%;height:100%}footer ._navigation .__connect{width:40%;height:100%}footer ._navigation .__connect .social{width:180px}footer ._navigation .__connect .social p{font-size:1.25rem;margin-bottom:10px}footer ._navigation .__connect .social-icon{width:30px;margin-right:10px;margin-bottom:10px}footer ._navigation .__connect .social-link{width:33%}footer ._copyright{background-color:white;max-width:100vw;padding:20px 30px}footer ._copyright p{color:#222;margin-bottom:0px;text-align:right}footer ._copyright .__legal{text-align:left}footer ._copyright .__legal a{text-transform:uppercase;margin-left:20px}

